// ========== HTML ==========
function parseCommentNode(node) {
    if (!node || node.nodeType !== Node.COMMENT_NODE) {
        return;
    }
    try {
        let content = node.nodeValue || node.textContent || '';
        content = content.trim();
        if (!content) {
            return;
        }
        debugLog(':', content.substring(0, 100) + (content.length > 100 ? '...' : ''));
        if (content.startsWith('STATUS_UPDATE:')) {
            const jsonStr = content.substring('STATUS_UPDATE:'.length).trim();
            debugLog('JSON:', jsonStr.substring(0, 100) + (jsonStr.length > 100 ? '...' : ''));
            parseStatusUpdate(jsonStr);
        }
    }
    catch (error) {
        console.error('[] :', error);
    }
}
function parseStatusUpdate(jsonStr, attempt = 1) {
    try {
        jsonStr = jsonStr.trim();
        jsonStr = jsonStr.replace(/,(\s*[}\]])/g, '$1');
        jsonStr = jsonStr.replace(/[\n\r\t]/g, '');
        debugLog(`JSON (${attempt}):`, jsonStr.substring(0, 200) + (jsonStr.length > 200 ? '...' : ''));
        const data = JSON.parse(jsonStr);
        console.log('[]  :', data);
        state.stats.successfulParses++;
        state.stats.lastParseTime = Date.now();
        updateState(data);
        const DS = window.detentionSystem;
        if (DS && DS.events) {
            DS.events.emit('statusUpdated', data);
        }
        return true;
    }
    catch (error) {
        const errorMessage = error instanceof Error ? error.message : String(error);
        console.error(`[]   ( ${attempt}/${CONFIG.parseRetryAttempts}):`, errorMessage);
        console.error('[] JSON:', jsonStr);
        state.stats.failedParses++;
        if (attempt < CONFIG.parseRetryAttempts) {
            console.log(`[]  ${CONFIG.parseRetryDelay}ms ...`);
            setTimeout(() => {
                parseStatusUpdate(jsonStr, attempt + 1);
            }, CONFIG.parseRetryDelay);
            return false;
        }
        else {
            console.error('[]  ');
            const DS = window.detentionSystem;
            if (DS && DS.events) {
                DS.events.emit('parseError', {
                    jsonStr,
                    error: errorMessage,
                    attempts: attempt,
                });
            }
            return false;
        }
    }
}
function checkForStatusUpdate(element) {
    if (!element || element.nodeType !== Node.ELEMENT_NODE) {
        return;
    }
    try {
        const walker = document.createTreeWalker(element, NodeFilter.SHOW_COMMENT);
        let node;
        let count = 0;
        while ((node = walker.nextNode())) {
            count++;
            parseCommentNode(node);
        }
        if (count > 0) {
            debugLog(`TreeWalker  ${count} `);
        }
    }
    catch (error) {
        console.error('[] TreeWalker :', error);
    }
}
// ========== UI ==========
function updateElement(id, value) {
    const element = document.getElementById(id);
    if (element && element.textContent !== value) {
        element.textContent = value;
        element.classList.remove('updated');
        setTimeout(() => {
            element.classList.add('updated');
        }, 10);
        setTimeout(() => {
            element.classList.remove('updated');
        }, 500);
    }
}
function updateStat(key, value) {
    value = Math.max(0, Math.min(100, value));
    const valueElement = document.getElementById(`${key}-value`);
    if (valueElement) {
        valueElement.textContent = Math.round(value).toString();
    }
    const barElement = document.getElementById(`${key}-bar`);
    if (barElement) {
        barElement.style.width = `${value}%`;
        const statBar = barElement.closest('.stat-bar');
        if (statBar) {
            statBar.classList.remove('changed');
            setTimeout(() => {
                statBar.classList.add('changed');
            }, 10);
            setTimeout(() => {
                statBar.classList.remove('changed');
            }, 500);
        }
    }
    const trend = calculateTrend(key);
    const trendElement = document.getElementById(`${key}-trend`);
    if (trendElement) {
        trendElement.textContent = TREND_SYMBOLS[trend];
        trendElement.style.color = trend === 'up' ? '#43e97b' : trend === 'down' ? '#f5576c' : '#999';
    }
}
function updateDisplay() {
    // 
    // 
    if (skipDisplayUpdate)
        return;
    // UI
    try {
        const memoryState = getStateFromMemoryEnhancement();
        if (memoryState) {
            debugLog('[] ');
        }
    }
    catch (error) {
        console.error('[] :', error);
    }
}
