function scanAllComments() {
    console.log('[] ...');
    const walker = document.createTreeWalker(document.body, NodeFilter.SHOW_COMMENT);
    const comments = [];
    let node;
    while ((node = walker.nextNode())) {
        const content = (node.nodeValue || node.textContent || '').trim();
        comments.push({
            content: content.substring(0, 100) + (content.length > 100 ? '...' : ''),
            fullContent: content,
            isStatusUpdate: content.startsWith('STATUS_UPDATE:'),
            length: content.length,
        });
        if (content.startsWith('STATUS_UPDATE:')) {
            console.log('[]  :', content.substring(0, 100));
            parseCommentNode(node);
        }
    }
    console.log(`[]  ${comments.length} `);
    console.log('[] :', comments.filter(c => c.isStatusUpdate).length);
    console.table(comments);
    return comments;
}
function getDiagnostics() {
    const diagnostics = {
        version: '3.5.6',
        timestamp: new Date().toLocaleString('zh-CN'),
        observer: {
            active: mutationObserver !== null,
            status: mutationObserver ? '' : '',
        },
        state: {
            lastUpdate: new Date(state.lastUpdate).toLocaleString('zh-CN'),
            timeSinceUpdate: Date.now() - state.lastUpdate,
            timeSinceUpdateFormatted: formatDuration(Date.now() - state.lastUpdate),
        },
        stats: {
            totalUpdates: state.stats.totalUpdates,
            successfulParses: state.stats.successfulParses,
            failedParses: state.stats.failedParses,
            successRate: state.stats.totalUpdates > 0
                ? `${((state.stats.successfulParses / state.stats.totalUpdates) * 100).toFixed(2)}%`
                : 'N/A',
            lastParseTime: state.stats.lastParseTime > 0 ? new Date(state.stats.lastParseTime).toLocaleString('zh-CN') : '',
        },
        currentState: StatusPanel.getState(),
        config: CONFIG,
    };
    console.log('[] ');
    console.log('[] :', diagnostics.version);
    console.log('[] :', diagnostics.timestamp);
    console.log('[] :', diagnostics.observer.status);
    console.log('[] :', diagnostics.state.lastUpdate);
    console.log('[] :', diagnostics.state.timeSinceUpdateFormatted);
    console.log('[] :', diagnostics.stats);
    console.log('[] :', diagnostics.currentState);
    return diagnostics;
}
